Button from '@/Components/Global/News/NewsTipButton.vue'\nimport UploadPressReleaseButton from '@/Components/Global/News/UploadPressReleaseButton.vue'\n\nconst appSettingStore = useAppSettingStore()\n\nconst props = defineProps({\n  newsCategories: Array,\n})\n</script>\n\n<style scoped>\n.category-card .w-60 {\n  transition: transform 0.3s ease-in-out;\n}\n\n.category-card:hover .w-60 {\n  transform: scale(1.05); /* Scales up the card to 105% of its original size on hover */\n}\n\n.category-card .h-60 {\n  transition: transform 0.3s ease-in-out;\n}\n\n.category-card:hover .h-60 {\n  transform: scale(1.05); /* Scales up the card to 105% of its original size on hover */\n}\n</style>\n"],"sourceRoot":""}]);
